.fp-animate--multiple-animation {
  display: inline-block;
}

@keyframes slide-in-left {
  0% {
    transform: translateX(-80px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes slide-in-right {
  0% {
    transform: translateX(80px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes swing-in-bottom {
  0% {
    transform: translateY(80px);
    opacity: 0;
  }
  80% {
    transform: translateY(-10px);
    opacity: 1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes swing-in-top {
  0% {
    transform: translateY(-80px);
    opacity: 0;
  }
  80% {
    transform: translateY(10px);
    opacity: 1;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes zoom-in {
  0% {
    transform: scale(0.975);
  }
  52.5% {
    transform: scale(0.975);
  }
  100% {
    transform: scale(1.025);
  }
}
@keyframes zoom-out {
  0% {
    transform: scale(1.025);
  }
  52.5% {
    transform: scale(1.025);
  }
  100% {
    transform: scale(0.975);
  }
}
.fp-animate--order-1 {
  transition-delay: 0ms !important;
  animation-delay: 0ms !important;
}
.fp-animate--order-2 {
  transition-delay: 250ms !important;
  animation-delay: 250ms !important;
}
.fp-animate--order-3 {
  transition-delay: 1s !important;
  animation-delay: 1s !important;
}
.fp-animate--order-4 {
  transition-delay: 2s !important;
  animation-delay: 2s !important;
}
.fp-animate--delay-1 {
  transition-delay: 1s !important;
  animation-delay: 1s !important;
}
.fp-animate--delay-2 {
  transition-delay: 2s !important;
  animation-delay: 2s !important;
}
.fp-animate--delay-3 {
  transition-delay: 3s !important;
  animation-delay: 3s !important;
}
.fp-animate--delay-4 {
  transition-delay: 4s !important;
  animation-delay: 4s !important;
}
.fp-animate--delay-5 {
  transition-delay: 5s !important;
  animation-delay: 5s !important;
}
.fp-animate--delay-6 {
  transition-delay: 6s !important;
  animation-delay: 6s !important;
}
.fp-animate--delay-7 {
  transition-delay: 7s !important;
  animation-delay: 7s !important;
}
.fp-animate--delay-8 {
  transition-delay: 8s !important;
  animation-delay: 8s !important;
}
.fp-animate--delay-9 {
  transition-delay: 9s !important;
  animation-delay: 9s !important;
}
.fp-animate--delay-10 {
  transition-delay: 10s !important;
  animation-delay: 10s !important;
}

body .fp-animate__fade_in {
  opacity: 0;
  transition: 400ms opacity ease-in-out;
}
body .fp-animate__fade_in--animate, body .fp-animate__fade_in--on-hover:hover {
  opacity: 1 !important;
}
body .fp-animate__fade_away {
  opacity: 1;
  transition: 400ms opacity ease-in-out;
  pointer-events: none;
}
body .fp-animate__fade_away--on-hover {
  pointer-events: all;
}
body .fp-animate__fade_away--animate, body .fp-animate__fade_away--on-hover:hover {
  opacity: 0 !important;
}
body .fp-animate__slide_in_left {
  transform: translateX(-80px);
  opacity: 0;
}
body .fp-animate__slide_in_left--animate {
  animation: slide-in-left 1.9s ease-in-out forwards;
}
body .fp-animate__slide_in_right {
  transform: translateX(80px);
  opacity: 0;
}
body .fp-animate__slide_in_right--animate {
  animation: slide-in-right 1.9s ease-in-out forwards;
}
body .fp-animate__swing_in_bottom {
  transform: translateY(80px);
  opacity: 0;
}
body .fp-animate__swing_in_bottom--animate {
  animation: swing-in-bottom 1s ease-in-out forwards;
}
body .fp-animate__swing_in_top {
  transform: translateY(-80px);
  opacity: 0;
}
body .fp-animate__swing_in_top--animate {
  animation: swing-in-top 1s ease-in-out forwards;
}
body .fp-animate__zoom_in {
  transform: scale(0.975);
  animation: zoom-out 350ms ease forwards;
}
body .fp-animate__zoom_in--animate, body .fp-animate__zoom_in--on-hover:hover {
  animation: zoom-in 350ms ease forwards;
}
body .fp-animate__zoom_in_alt {
  transform: scale(0.975);
  transition: transform 350ms ease 150ms;
}
body .fp-animate__zoom_in_alt--animate, body .fp-animate__zoom_in_alt--on-hover:hover {
  transform: scale(1.025);
}
body .fp-animate__zoom_out {
  transform: scale(1.025);
  animation: zoom-in 350ms ease forwards;
}
body .fp-animate__zoom_out--animate, body .fp-animate__zoom_out--on-hover:hover {
  animation: zoom-out 350ms ease forwards;
}
body .fp-animate__zoom_out_alt {
  transform: scale(1.025);
  transition: transform 350ms ease 150ms;
}
body .fp-animate__zoom_out_alt--animate, body .fp-animate__zoom_out_alt--on-hover:hover {
  transform: scale(0.975);
}

.editor-styles-wrapper .wp-block:not(.wp-block-fpbk-animate) .fp-animate {
  opacity: 1 !important;
  pointer-events: all !important;
}

.editor-styles-wrapper .wp-block-fpbk-animate {
  box-shadow: 0 0 0 1px #3c434a;
}
.editor-styles-wrapper .wp-block-fpbk-animate__playback-control {
  right: 0;
  bottom: 0;
}
.editor-styles-wrapper .wp-block-fpbk-animate__playback-control button:focus {
  box-shadow: none !important;
}
.editor-styles-wrapper .wp-block-fpbk-animate__playback-control .dashicon {
  margin-right: 2px !important;
}

.no-animations {
  transition: none !important;
  animation: none !important;
}